<template>
  <div class="content">
    <!-- Item ID by Inject: {{ id }} -->
    {{ id }}
    <br>
    <!-- Item ID by Prop: {{ itemId }} -->
  </div>
</template>

<script>
export default {
  inject: { $item: { default: null } },
  // props: {
  //   itemId: { type: [Number, String] }
  // },
  computed: {
    itemByinject() {
      if (this.$item) {
        return this.$item();
      }
      return null;
    },
    id() {
      if (this.itemByinject) {
        return this.itemByinject.id;
      }
      return "";
    }
  }
};
</script>

<style>
.content {
  height: 100%;
  width: 100%;
	display: flex;
  align-items: center;
  justify-content: center
}
</style>


